function build() {
    const { robot } = require('./robot');
    const serverProxy = require('./server-proxy');
    const {
        isBoolean, isInteger, isEnum, isString, checkFilter,
    } = require('./common');
    const { robotStatus, robotType, JobStatus } = require('./enums');

    const enums = require('./enums');

    const {
        Exception, ArgumentError, DataError, ScriptingError, ServerError,
    } = require('./exceptions');
    const pjson = require('../package.json');
    const config = require('./config');

    const toy = {
        server: {
            async request(url, data) {
                const result = await serverProxy.server.request(url, data);
                return result;
            },
        },
        robots: {
            async get(filter = {}) {
                checkFilter(filter, {
                    page: isInteger,
                    name: isString,
                    id: isInteger,
                    owner: isString,
                    search: isString,
                    status: isEnum.bind(robotStatus),
                    type: isEnum.bind(robotType),
                });

                if ('search' in filter && Object.keys(filter).length > 1) {
                    if (!('page' in filter && Object.keys(filter).length === 2)) {
                        throw new ArgumentError('Do not use the filter field "search" with others');
                    }
                }

                if ('id' in filter && Object.keys(filter).length > 1) {
                    if (!('page' in filter && Object.keys(filter).length === 2)) {
                        throw new ArgumentError('Do not use the filter field "id" with others');
                    }
                }

                const searchParams = new URLSearchParams();
                for (const field of ['name', 'owner', 'search', 'status', 'type', 'id', 'page']) {
                    if (Object.prototype.hasOwnProperty.call(filter, field)) {
                        searchParams.set(field, filter[field]);
                    }
                }
                const robotsData = await serverProxy.robots.getrobots(searchParams.toString());
                const robots = robotsData.map((robot) => new robot(robot));

                robots.count = robotsData.count;

                return robots;
            },
        },
        config: {
            get backendAPI() {
                return config.backendAPI;
            },
            set backendAPI(value) {
                config.backendAPI = value;
            },
        },
        client: {
            version: `${pjson.version}`,
        },
        enums,
        exceptions: {
            Exception,
            ArgumentError,
            DataError,
            ScriptingError,
            ServerError,
        },
        classes: {
            robot,
        },
    };

    toy.server = Object.freeze(toy.server);
    toy.robots = Object.freeze(toy.robots);
    toy.client = Object.freeze(toy.client);
    toy.enums = Object.freeze(toy.enums);

    return toy;
}

module.exports = build();
